<template>
<!-- <모달창> -->
  <div class="bg-black" v-if="modalwin==true">
    <div class="bg-white">
    <iframe width="100%" :src="data.video" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
    <!-- <img :src="data.img"/> / -->
    <h3> {{data.title}} </h3>
      <!-- <img :src="data.img"> -->
      <input v-model.number="inputText"> {{ inputText}}
    
      <p>
        <button @click="$emit('modalClose')"> 닫기 </button>
      </p>
    </div>
  </div>
</template>

<script>
export default {
    name:"ModalData",
    data(){
        return{
            inputText : "",
        }
    },
    watch:{
        inputText(e){
            if (Number( e) > 13){
                alert('error occurse')
                this.inputText=""
            }
            else if(isNaN(e) == true){
                alert('no string')
                this.inputText=""

            }
        }
    },
    props:{ 
        modalwin:Boolean,
        data : Object,
    },
}
</script>

<style>
.bg-black{
  width: 100%; height:100%;
  background: rgba(0,0,0,0.5);
  position: fixed;
  padding:20px;
}
.bg-white{
  background-color: white;
  width: 350px;
  border-radius: 20px;
  padding:20px;
}
.bg-white img{
    width:100%;
}
</style>